
[class*="-enter-active"] {
  -webkit-animation: slideInUp 500ms;
          animation: slideInUp 500ms;
}
.starfield {
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  will-change: content;
  width: 4500rem;
  height: 2048rem;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.starfield::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.01;
    -webkit-transition: opacity 500ms;
    transition: opacity 500ms;
}
.lit .starfield::before {
      opacity: 0.1;
}
.viewing-sky .starfield::before {
      opacity: 0.2;
}
[id="planisphere"] {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  -webkit-transition: opacity 250ms;
  transition: opacity 250ms;
}
.viewing-sky [id="planisphere"] {
    opacity: 1;
}
.planisphere {
  width: 2250rem;
  height: 1024rem;
  opacity: 0;
  -webkit-transition: opacity 1000ms;
  transition: opacity 1000ms;
}
.planisphere::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 200%;
    height: 200%;
    pointer-events: none;
    opacity: 0.33;
    background: url("../assets/images/galaxy.png");
    background-size: 2250rem 1024rem;
}
.viewing-sky .planisphere {
    opacity: 1;
    -webkit-transition: opacity 500ms;
    transition: opacity 500ms;
}



